//    / (Windows-safe, cross-platform)
import fs from "fs";
import path from "path";

const ILLEGAL = /[<>:"/\\|?*\x00-\x1F]/g; //  Win- + control
const DOTS_SPACES_EDGES = /^[.\s]+|[.\s]+$/g; // /  /
const RESERVED = /^(con|prn|aux|nul|com[1-9]|lpt[1-9])$/i; //  

function clipByBytes(str, maxBytes = 240) {
	//    ~240  UTF-8,    255+  NTFS
	let out = "";
	let bytes = 0;
	for (const ch of str) {
		const b = Buffer.byteLength(ch, "utf8");
		if (bytes + b > maxBytes) break;
		out += ch;
		bytes += b;
	}
	return out;
}

export function sanitizeFsName(input, replacement = "-") {
	let s = String(input || "")
		.normalize("NFKC") //  
		.replace(ILLEGAL, replacement) //  
		.replace(/\s+/g, " ") //  
		.replace(/-{2,}/g, "-") //    
		.trim()
		.replace(DOTS_SPACES_EDGES, ""); //  /  

	if (s === "." || s === ".." || RESERVED.test(s)) s = `${s}-item`;
	s = clipByBytes(s, 240);
	if (!s) s = "untitled";
	return s;
}

//    ,  " (2)", " (3)"  
export function ensureUniqueDir(baseDir, desiredName) {
	const safe = sanitizeFsName(desiredName);
	let name = safe;
	let i = 2;
	let p = path.join(baseDir, name);
	while (fs.existsSync(p)) {
		name = `${safe} (${i++})`;
		p = path.join(baseDir, name);
	}
	return { dirPath: p, dirName: name, safeBaseName: safe };
}

export async function ensureUniqueDirAsync(baseDir, desiredName) {
	const safe = sanitizeFsName(desiredName);
	let name = safe;
	let i = 2;
	let p = path.join(baseDir, name);

	//   
	while (
		await fs.promises
			.stat(p)
			.then(() => true)
			.catch(() => false)
	) {
		name = `${safe} (${i++})`;
		p = path.join(baseDir, name);
	}

	return { dirPath: p, dirName: name, safeBaseName: safe };
}
