import Vue from "vue";

export default {
	namespaced: true,
	state: () => ({
		loaders: {}, //   
	}),
	getters: {
		//  : true,      
		isLoading: (state) => Object.keys(state.loaders).length > 0,

		//    
		isLoadingByKey: (state) => (key) => !!state.loaders[key],

		//    
		isLoadingByKeys: (state) => (keys) =>
			keys.some((k) => !!state.loaders[k]),
	},
	mutations: {
		START(state, key) {
			Vue.set(state.loaders, key, true);
		},
		STOP(state, key) {
			Vue.delete(state.loaders, key);
		},
		RESET(state) {
			state.loaders = {};
		},
	},
	actions: {
		start({ commit }, key) {
			commit("START", key);
		},
		stop({ commit }, key) {
			commit("STOP", key);
		},
		reset({ commit }) {
			commit("RESET");
		},
	},
};
